import axios from 'axios';
import { Message } from 'element-ui';
import store from '@/store/store';
import router from '@/router';
import { removeToken, removeName } from '@/utils/token';

//  axios 
const service = axios.create({
  baseURL: 'http://172.16.10.118:8011/', //  url
  timeout: 10000, // 
  // form-data
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
  },
  // ;   
  transformRequest: [
    function (data) {
      let ret = '';
      for (const it in data) {
        ret +=
          encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&';
      }
      ret = ret.slice(0, -1); // 
      return ret;
    }
  ]
});

// asiox 
service.interceptors.request.use(
  config => {
    // 
    /* if (store.getters.token) {
    config.headers['X-Token'] = 'test'; // token-- ['X-Token']key 
  } */
    if (config.url !== '/user_app/login') {
      config.data['token'] = 'test';
      config.data['user_name'] = 'test';
      /* if (store.getters.token && store.getters.name) {
        config.data['token'] = 'test';
        config.data['user_name'] = store.getters.name;
      } */
    }
    return config;
  },
  error => {
    // 
    console.log(error); // for debug
    Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  response => {
    const res = response.data;
    if (res.status === 0) {
      return res;
    } if (res.status === 40004) {
      removeToken();
      removeName();
      router.push('/login');
      Message({
        message: res.info,
        type: 'error',
        duration: 5 * 1000
      });
      return null;
    } else {
      Message({
        message: res.info,
        type: 'error',
        duration: 5 * 1000
      });
      return null;
    }
  },

  /**
   * responsecodecode
   * xmlhttprequest  error
   */
  //  const res = response.data;
  //     if (res.code !== 20000) {
  //       Message({
  //         message: res.message,
  //         type: 'error',
  //         duration: 5 * 1000
  //       });
  //       // 50008:token; 50012:;  50014:Token ;
  //       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //         MessageBox.confirm('', '', {
  //           confirmButtonText: '',
  //           cancelButtonText: '',
  //           type: 'warning'
  //         }).then(() => {
  //           store.dispatch('FedLogOut').then(() => {
  //             location.reload();// vue-router bug
  //           });
  //         })
  //       }
  //       return Promise.reject('error');
  //     } else {
  //       return response.data;
  //     }
  error => {
    console.log('err' + error); // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    });
    return Promise.reject(error);
  }
);

export default service;



